import uniCollapse from '@/colorui/components/uni-collapse.vue'
import uniCollapseItem from '@/colorui/components/uni-collapse-item.vue'
import misEnum from '../../common/mis-enum.js';
export default {
	components: {
		uniCollapse,
		uniCollapseItem
	},
	data() {
		return {
			stars: [1, 2, 3, 4, 5],
			info: {},
			list: [],
			fillMileage: 0,
			showMileageModal: false,
			orderIndex: 0,
			fillDays: 0,
			showDaysModal: false
		}
	},
	onLoad(option) {
		var info = JSON.parse(decodeURIComponent(option.para));
		this.info = info;
		//获取派车单
		if (this.info.status >= 2) {
			global.$http.post('/car/dispatch/getListByDispatch', {
				params: {
					order_code: this.info.order_code
				},
			}).then(res => {
				if (res.status === "0") {
					this.list = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	},
	methods: {
		getCarListData() {
			this.status = 'loading';
			global.$http.post('/car/dispatch/dispatchCarList', {
				params: {
					order_code: this.info.order_code,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.cars = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getDriverListData() {
			this.status = 'loading';
			global.$http.post('/car/dispatch/dispatchDriverList', {
				params: {
					order_code: this.info.order_code,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.drivers = res.data;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getTitle: function(totalFee) {
			return "总费用(元)：" + totalFee;
		},
		getStatusStr: function(status) {
			return misEnum.DispatchRecordEnumMap.get(status);
		},
		sureSettle: function(order){
			uni.showModal({
				title: '提示',
				content: '确认结算该派车单？',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if(res.cancel) return;
					uni.showLoading({
						title: '提交中',
						mask: false
					});
					global.$http.post('/car/dispatch/sureAccount', {
						params: {
							dispatch_id: order.id
						},
					}).then(res => {
						uni.hideLoading();
						if (res.status === "0") {
							uni.showToast({
								title: "提交成功",
								icon: 'none'
							});
							//设置状态
							this.list.forEach(c=>{
								if(c.id == order.id){
									c.status = 4;
									return;
								}
							});
						} else {
							uni.showToast({
								title: res.msg,
								icon: 'none'
							});
						}
					}).catch(err => {
						uni.hideLoading();
						uni.showToast({
							title: err.message,
							icon: 'none'
						});
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		hideDaysModal: function(){
			this.showDaysModal = false;
		},
		fillDaysInput: function(e){
			this.fillDays = e.detail.value;
		},
		sureDaysModal:function(){
			if(this.fillDays < 0){
				uni.showToast({
					title: "用车天数不能小于0",
					icon: 'none'
				});
				return;
			}
			this.showDaysModal = false;
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			var order = this.list[this.orderIndex];
			global.$http.post('/car/dispatch/update', {
				params: {
					id: order.id,
					days: this.fillDays,
					stop_num: order.stop_num
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					order.days = this.fillDays;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		hideMileageModal: function(){
			this.showMileageModal = false;
		},
		fillMileageInput: function(e){
			this.fillMileage = e.detail.value;
		},
		sureMileageModal:function(){
			if(this.fillMileage < 0){
				uni.showToast({
					title: "里程数不能小于0",
					icon: 'none'
				});
				return;
			}
			this.showMileageModal = false;
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			var order = this.list[this.orderIndex];
			global.$http.post('/car/dispatch/update', {
				params: {
					id: order.id,
					days: order.days,
					stop_num: this.fillMileage
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					order.stop_num = this.fillMileage;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		editMileage: function(index){
			this.orderIndex = index;
			this.fillMileage = this.list[index].stop_num;
			this.showMileageModal = true;
		},
		editDays: function(index){
			this.orderIndex = index;
			this.fillDays = this.list[index].days;
			this.showDaysModal = true;
		}
	}
}
